import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography } from "@material-ui/core";
import QRCode from "react-qr-code";
import Popup from "../Popup";
import Controls from "../controls/Controls";
import CIcon from "@coreui/icons-react";

const useStyles = makeStyles((theme) => ({
  main: {
    ...theme.GlobalBox,
    height: 100,
    maxHeight: 100,
    display: "flex",
  },
  info: {
    padding: 16,
    flexGrow: 1,
  },
}));

export default function ShopInfo(props) {
  const [url, seturl] = useState("");
  const [openPopup, setOpenPopup] = useState(false);
  const classes = useStyles(props);

  const genarate = async () => {
    const publicIp = require("public-ip");
    const url = await publicIp.v4();
    seturl(`https://${url}:4141`);
    if(url){
        setOpenPopup(true);
    }
  };
  return (
    <>
      <Paper className={classes.main}>
        <CIcon
          name={"tab"}
          height={100}
          style={{
            margin: 2,
            padding: 8,
          }}
        />
        <div className={classes.info}>
          <Typography variant="body1" className={classes.heading}>
            Scan your Tablet QR Code
          </Typography>
          <Typography variant="body2" className={classes.secondaryHeading}>
            Genarate employe wireless Tablet url
          </Typography>
          <div>
            <Controls.Button text="Genarate" onClick={genarate} />
          </div>
        </div>
      </Paper>
      <Popup
        title={"QR Code "}
        subTitle={"wireless user interface"}
        icon={"qr"}
        openPopup={openPopup}
        setOpenPopup={setOpenPopup}
      >
        <QRCode value={url} />
      </Popup>
    </>
  );
}
