import React, {useState, useEffect} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
} from '@coreui/react'
import { LogOut, getProfileFetch } from '../store/action/Auth'
import { connect } from 'react-redux'
import CIcon from '@coreui/icons-react'
// import nav from './_nav'

const TheSidebar = ({Auth}) => {
  const [navigation , setNavigation] = useState([])
  const [admin , setAdmin] = useState(false)
  const dispatch = useDispatch()
  const show = useSelector(state => state.changeState.sidebarShow)
  const { currentUser } = Auth
  
  useEffect(() => {
    if(currentUser){
      setNavigation(currentUser.navigation)
      setAdmin(currentUser.admin)
    }
  }, [currentUser])

  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({ type: 'set', sidebarShow: val })}
      minimize={true}
    >
      <CSidebarBrand className="d-md-down-none" to={admin ? "/GrowPos" :"/"}>
        <CIcon
          name="logo"
          src={'http://localhost:4545/static/img/logoFullLite.png'}
          height={35}
          className="c-sidebar-brand-full"
        />
        <CIcon
          className="c-sidebar-brand-minimized"
          name="logo"
          src={'http://localhost:4545/static/img/logo.png'}
          height={35}
        />

      </CSidebarBrand>
      <CSidebarNav>
        <CCreateElement
          items={navigation}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none" />
    </CSidebar>
  )
}
const mapStateToProps = (state) => {
  return {
    Auth: state.Auth,
  }
}
export default React.memo(connect(mapStateToProps, { LogOut, getProfileFetch })(TheSidebar))
