import React, { useEffect, useContext } from "react";
import { connect } from "react-redux";
import { ShopHandeler } from "../../../../LocalDB/ShopDB";
import { makeStyles } from "@material-ui/core/styles";
import {
  Paper,
  List,
  ListItem,
  ListItemIcon,
  ListItemSecondaryAction,
  ListItemText,
  ListSubheader,
  Switch,
  Grid,
  Typography,
} from "@material-ui/core";
import CIcon from "@coreui/icons-react";
import nav from "../../../../containers/_nav";
import Controls from "../../../../components/controls/Controls";
import PublishIcon from "@material-ui/icons/Publish";
import Notification from "../../../../components/Notification";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    // maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  main: (props) => {
    return {
      ...theme.GlobalBox,
      overflow: "hidden",
      height: `${props.height - 68}px`,
      width: "100%",
    };
  },
  Body: (props) => {
    return {
      ...theme.GlobalBox,
      overflow: "auto",
      height: `${props.height - 68}px`,
      width: "100%",
    };
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "40%",
    flexShrink: 0,
    alignItems: "center",
    borderBottom: "1px solid #f0f0f0",
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
    alignItems: "center",
    borderBottom: "1px solid #f0f0f0",
  },
  titleIcon: {
    backgroundColor: theme.palette.secondary.light,
    color: theme.palette.secondary.main,
    "&:hover": {
      backgroundColor: theme.palette.secondary.light,
      cursor: "default",
    },
    "& .MuiSvgIcon-root": {
      fontSize: "8rem",
    },
  },
  Info: {
    padding: theme.spacing(2),
    borderRadius: 0,
    border: 0,
    boxShadow: "0 0px 0px 0px ",
    height: "100%",
    width: "100%",
    borderTop: "1px solid #f0f0f0",
    background: "#00000000",
    backgroundColor: "#00000000",
  },
  Title: {
    textAlign: "center",
  },
  Content: {
    textAlign: "center",
  },
  avatar: {
    backgroundColor: "#00000000",
    hiight: 100,
    width: 100,
  },
  newButton: {
    right: "8px",
    top: "8px",
    position: "absolute",
  },
  heading: {
    fontSize: theme.typography.pxToRem(18),
    flexBasis: "33.33%",
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(14),
  },
  Header: {
    padding: "8px",
    borderBottom: "1px solid #f0f0f0",
  },
}));

const Permission = (props) => {
  const { editUser , loadAllusers} = useContext(ShopHandeler);

  const { current } = props;
  const classes = useStyles();
  const [notify, setNotify] = React.useState({
    isOpen: false,
    message: "",
    type: "",
  });
  const [load, setLoad] = React.useState(false);
  const [checked, setChecked] = React.useState([]);
  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  useEffect(() => {
    if (!load) {
      let userNav = checked;
      current.navigation.forEach((element) => {
        userNav.push(element.name);
      });
      setChecked(userNav);
      setLoad(true);
    }
  }, [current.navigation, load, checked]);

  const handleUpdate = () => {
    let newNavigation = [];
    checked.forEach((element) => {
      const filterData = nav.find((item) => item.name === element);
      console.log(filterData);
      newNavigation.push(filterData);
    });

    const newData = Object.assign(current, {
      navigation: newNavigation,
    });
    console.log("[newData]", newData);
    console.log("[current]", current);
    editUser(current._id, newData).then(async (d) => {
      await setNotify({
        isOpen: true,
        message: `This change effects on after re login`,
        type: "success",
      });
      await loadAllusers()
      await setLoad(false);
    });
  };
  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={6} id="FormCategory" className={classes.main}>
        <Paper className={classes.Info}>
          <div className={classes.Title}>
            <img
              src={"http://localhost:4545/static/img/permission.png"}
              className={classes.avatar}
              alt="icon"
            />
          </div>
          <div className={classes.Content}>
            <Typography variant="h6">Setup Your User Permission</Typography>
            <Typography variant="body2" display="inline">
              Protect your business from fraud
            </Typography>
          </div>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={6} id="ViewCategory" className={classes.main}>
        <Paper className={classes.Body}>
          <List
            subheader={
              <ListSubheader className={classes.Header}>
                <Typography variant="body1" className={classes.heading}>
                  {current.EmpolyeName}
                </Typography>
                <Typography
                  variant="body2"
                  className={classes.secondaryHeading}
                >
                 {current.Department}
                </Typography>
                <Controls.Button
                  text="Update"
                  variant="outlined"
                  startIcon={<PublishIcon />}
                  className={classes.newButton}
                  onClick={handleUpdate}
                />
              </ListSubheader>
            }
            className={classes.root}
          >
            {nav.map((item) => (
              <ListItem>
                <ListItemIcon>
                  <CIcon name={item.icon} height={35} />
                </ListItemIcon>
                <ListItemText id={item._tag} primary={item.name} />
                <ListItemSecondaryAction>
                  <Switch
                    edge="end"
                    onChange={handleToggle(item.name)}
                    checked={checked.indexOf(item.name) !== -1}
                    inputProps={{ "aria-labelledby": item._tag }}
                  />
                </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
          <Notification notify={notify} setNotify={setNotify} />
        </Paper>
      </Grid>
    </Grid>
  );
};

const mapStateToProps = (state) => {
  return {
    data: state.DataStore,
    sync: state.SyncData,
    Auth: state.Auth,
    Shop: state.Shop,
  };
};
export default connect(mapStateToProps)(Permission);
