import React, { useEffect, useState } from "react";
import { Grid, Paper } from "@material-ui/core"; //Chip
import Controls from "../../../../components/controls/Controls";
import { useForm } from "../../../../components/useForm";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { connect } from "react-redux";
import Notification from "../../../../components/Notification";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import PropTypes from "prop-types";
import axios from "axios";
import { updateUser, isAuth, getCookie } from "../../../../helpers/auth";

const AntTabs = withStyles((theme) => ({
  root: {
    borderBottom: "1px solid #e8e8e8",
  },
  indicator: {
    backgroundColor: theme.palette.primary.main,
  },
}))(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:hover": {
      color: theme.palette.primary.main,
      opacity: 1,
    },
    "&$selected": {
      color: theme.palette.primary.main,
      fontWeight: theme.typography.fontWeightMedium,
    },
    "&:focus": {
      color: theme.palette.primary.main,
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: theme.palette.background.paper,
  },

  Header: (props) => {
    return {
      ...theme.GlobalBox,
      border: 0,
      padding: 8,
      height: `${48}px`,
      width: "100%",
      // borderBottom: '1px solid #f0f0f0'
    };
  },
  Body: (props) => {
    return {
      ...theme.GlobalBox,
      overflow: "auto",
      // height: `${(props.height) - 110}px`,
      height: "100%",
      padding: 8,
    };
  },
  Footer: (props) => {
    return {
      ...theme.GlobalBox,
      padding: 5,
      width: "100%",
      height: `${48}px`,
      borderTop: `1px solid ${theme.palette.divider}`,
    };
  },
  heading: {
    fontSize: theme.typography.pxToRem(10),
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <Box>{children}</Box>}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

const Shop = (props) => {
  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });
  const [isActive, setIsActive] = useState(false);
  const [fetchData, setfetchData] = useState(false);
  const initialFValues = {
    name: "",
    email: "",
    location: "",
    contact: "",
    password1: "",
    textChange: "Update",
    role: "",
  };
  const validate = (fieldValues = values) => {
    let temp = { ...errors };
    if ("Name" in fieldValues)
      temp.Name = fieldValues.Name ? "" : "This field is required.";
    if ("Type" in fieldValues)
      temp.Type = fieldValues.Type ? "" : "This field is required.";
    if ("Contact" in fieldValues)
      temp.Contact = fieldValues.Contact ? "" : "This field is required.";
    if ("Location" in fieldValues)
      temp.Location = fieldValues.Location ? "" : "This field is required.";
    setErrors({
      ...temp,
    });

    if (fieldValues === values)
      return Object.values(temp).every((x) => x === "");
  };
  const {
    values,
    setValues,
    errors,
    setErrors,
    handleInputChange,
    resetForm,
  } = useForm(initialFValues, true, validate);

  const handleSubmit = (e) => {
    e.preventDefault();
    const token = getCookie("token");
    axios
      .put(
        `${process.env.REACT_APP_API_URL}/user/update`,
        { ...values, active: true },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        getData()
        updateUser(res, () => {
          setNotify({
            isOpen: true,
            message: "Submitted Successfully",
            type: "success",
          });
        });
      })
      .catch((err) => {
        setNotify({
          isOpen: true,
          message: "something went wrong",
          type: "error",
        });
      });
  };

  const getData = () => {
    const token = getCookie("token");
    axios
      .get(`${process.env.REACT_APP_API_URL}/user/${isAuth()._id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        const { role, name, email, location, contact, walletAmount } = res.data;
        setValues({
          ...values,
          role,
          name,
          email,
          location,
          contact,
          walletAmount,
        });
        setIsActive(res.data.active);
        setfetchData(false);
      })
      .catch((err) => {
        console.error(`Error To Your Information ${err.response.statusText}`);
        if (err.response.status === 401) {
        }
        setIsActive(false);
        setfetchData(true);
        setNotify({
          isOpen: true,
          message: "something went wrong or no internet",
          type: "error",
        });
      });
  };
  useEffect(() => {
    getData()
  }, []);

  const classes = useStyles(props);
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <>
      <div className={classes.root}>
        <div className={classes.demo1}>
          <AntTabs
            value={value}
            onChange={handleChange}
            aria-label="ant example"
          >
            <AntTab label="User Profile" {...a11yProps(0)} />
          </AntTabs>
        </div>
        <TabPanel value={value} index={0}>
          <Paper className={classes.Body}>
            <Grid container spacing={1} style={{ padding: 8 }}>
              <Grid item xs={12} sm={12}>
                <Grid container spacing={1}>
                  <Grid item xs={12} sm={12}>
                    <Controls.Input
                      name="name"
                      label="Shop Name"
                      type="text"
                      value={values.name}
                      size="small"
                      fullWidth
                      error={errors.name}
                      onChange={handleInputChange}
                    />
                  </Grid>
                  <Grid item xs={12} sm={12}>
                    <Grid item xs={12} sm={12}>
                      <Controls.Input
                        name="email"
                        label="Shop email"
                        type="text"
                        value={values.email}
                        size="small"
                        fullWidth
                        error={errors.email}
                        onChange={handleInputChange}
                      />
                    </Grid>
                  </Grid>
                  <Grid item xs={6} sm={6}>
                    <Controls.Input
                      name="contact"
                      label="Contact"
                      type="text"
                      size="small"
                      fullWidth
                      error={errors.contact}
                      value={values.contact}
                      onChange={handleInputChange}
                    />
                  </Grid>
                  <Grid item xs={6} sm={6}>
                    <Controls.Input
                      name="location"
                      label="Location"
                      type="text"
                      size="small"
                      fullWidth
                      error={errors.location}
                      value={values.location}
                      onChange={handleInputChange}
                    />
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Paper>
        </TabPanel>
      </div>
      <Paper className={classes.Footer}>
        <div>
          <Controls.Button
            type="submit"
            text={isActive ? "Update" : "Active account"}
            color="primary"
            disabled={fetchData}
            onClick={handleSubmit}
          />
          <Controls.Button text="Reset" color="default" onClick={resetForm} />
        </div>
      </Paper>
      <Notification notify={notify} setNotify={setNotify} />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    Shop: state.Shop,
  };
};

export default connect(mapStateToProps)(Shop);
