import React from 'react'
import {  TextField as MuiSelect, MenuItem, makeStyles } from '@material-ui/core';
const useStyles = makeStyles(theme => ({
    input: {
        borderRadius: '25px',
        color: '#321fdb',
    },
    select:{
        borderRadius: '25px',
    }
}))
export default function Select(props) {
    const classes = useStyles();
    const { name, label, value, error=null,  onChange, options, optionsValue, optionsDisplay, ...other } = props;
    return (
        <MuiSelect
            label={label}
            name={name}
            value={value}
            select
            variant="outlined"
            onChange={onChange}
            InputProps={{
                classes: {
                    root: classes.input,
                },
            }}
            {...other}
            {...(error && {error:true,helperText:error})}
        >
            <MenuItem value="" >None</MenuItem>
            {
                options.map(
                    item => (<MenuItem key={item._id} value={item[optionsValue]}>{item[optionsDisplay]}</MenuItem>)
                )
            }
        </MuiSelect>

    )
}
