import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography, InputAdornment } from "@material-ui/core";
import Controls from "../controls/Controls";
import CIcon from "@coreui/icons-react";
import LanguageIcon from "@material-ui/icons/Language";


const useStyles = makeStyles((theme) => ({
  main: {
    ...theme.GlobalBox,
    height: 100,
    maxHeight: 100,
    display: "flex",
  },
  info: {
    padding: 16,
    flexGrow: 1,
  },
  searchInput: {
    borderRadius: 25,
    margin: 6,
  },
  resize: {
    borderRadius: "25px",
    height: 27,
    fontSize: 14,
    padding: "0 0px 0px 0px ",
  },
  input: {
    borderRadius: "25px",
    color: "#321fdb",
  },
}));

export default function Support(props) {
  const [value, setValue] = useState("web.growpos.in/tfsddj");
  const [status, setStatus] = useState(false);
  const classes = useStyles(props);
  const { label, sublabel } = props;

  return (
    <>
      <Paper className={classes.main}>
        <CIcon
          name={"web"}
          height={100}
          style={{
            margin: 2,
            padding: 8,
          }}
        />
        <div className={classes.info}>
          {/* <Typography variant="body1" className={classes.heading}>
            Share your website URL
          </Typography> */}
          <div>
            <Controls.Input
              size="small"
              fullWidth
              className={classes.searchInput}
              value={value}
              InputProps={{
                classes: { input: classes.resize, root: classes.input },
                startAdornment: (
                  <InputAdornment
                    position="start"
                    size="small"
                    style={{ padding: 2 }}
                  >
                    <LanguageIcon fontSize="inherit" />
                  </InputAdornment>
                ),
              }}
            />
           
              <Controls.Button text={status ? 'Copy Done!' :"Copy URL"} />
    
          </div>
        </div>
      </Paper>
    </>
  );
}

